/*start  define default css variable values */
:root {
	--main-bgc: white;
	--main-title-c: black;
	--main-text-c: gray;
}
/*end  */

/*start  define custom classes to change css variable values when a ssign it to html or body tag*/
.green-theme {
	--main-bgc: #7fc8a9;
	--main-title-c: #5f7a61;
	--main-text-c: #d5eebb;
}

.blue-theme {
	--main-bgc: #39a2db;
	--main-title-c: #161d6f;
	--main-text-c: #a3d8f4;
}

.red-theme {
	--main-bgc: #d83a56;
	--main-title-c: #810000;
	--main-text-c: #ffb0b0;
}

.brown-theme {
	--main-bgc: #b4846c;
	--main-title-c: #7d5a50;
	--main-text-c: #ffd8cc;
}

.orange-theme {
	--main-bgc: #f7a440;
	--main-title-c: #e1701a;
	--main-text-c: #f6dcbf;
}

.purple-theme {
	--main-bgc: #892cdc;
	--main-title-c: #52057b;
	--main-text-c: #bc6ff1;
}
/*end  */

body {
	font-family: Rubik, sans-serif;
	/* background-color: #ccc; */
	background: #c9d6ff; /* fallback for old browsers */
	background: -webkit-linear-gradient(
		to left,
		#e2e2e2,
		#c9d6ff
	); /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to left,
		#e2e2e2,
		#c9d6ff
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

/*start  create custom classes*/
.main-title {
	color: #93b5c6;
}

.fit-content {
	width: fit-content;
}

.pointer-cursor {
	cursor: pointer;
}
/*end*/

footer {
	color: #f1e9e5;
}

/*start  style sec element with by using css variable to change colors */
.sec {
	background-color: var(--main-bgc);
}

.sec-title {
	color: var(--main-title-c);
}

.sec-text {
	color: var(--main-text-c);
}
/*end  */

/*start style aside color panel */
aside.color-panel {
	z-index: 9999;
	width: 170px;
	transform: translateX(-100%); /*to hide it when start*/
	transition: transform 0.4s ease-in-out; /*to apply Smooth transition  when show & hide*/
}

aside.color-panel.open {
	transform: translateX(0%);
}

[data-color] {
	width: 30px;
	height: 30px;
}

.bgc-green {
	background-color: #5f7a61;
}

.bgc-red {
	background-color: #810000;
}

.bgc-blue {
	background-color: #161d6f;
}

.bgc-brown {
	background-color: #7d5a50;
}

.bgc-orange {
	background-color: #e1701a;
}

.bgc-purple {
	background-color: #52057b;
}
/*end*/

.fa-gears {
	pointer-events: none;
}
